body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    margin: 0;
    padding: 0;
}

i{
    cursor: pointer;
}

body{
    overflow-x: hidden;
}

/* **************************************** */

.main-body{
    width: 100vw;
    height: 1000px;
    background-color: pink;
}

/* **************************************** */

.bottom-bar{
    width: 100vw;
    height: 80px;
    background-color: #fff;
    position: -webkit-sticky;
    position: sticky;
    top: 0px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    box-shadow: 2px 2px 2px rgb(104,245,48);
    z-index: 4;
}
.bottom-bar li{
    display: inline-block;
    width: 70px;
    height: 50px;
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: black;
    padding: 5px 10px;
    position: relative;
}
.bottom-bar li i{
    color: black;
    font-size: 1.3rem;
    margin-left: 10px;
}
.bottom-bar li a{
    text-decoration: none;
    color: black;
    margin-left: 10px;
}

/* ******************************* */

.bottom-bar li:hover::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: transparent;
    -webkit-animation: lineAnimation 2s infinite;
            animation: lineAnimation 2s infinite;
    border-radius: 2px; /* Adjust the value to match the height of the line */
  }
  
  @-webkit-keyframes lineAnimation {
    0% {
      background-color: #00ff00; 
      -webkit-transform: scaleX(0); 
              transform: scaleX(0);
    }
    25% {
      background-color: #00ff00; 
      -webkit-transform: scaleX(1); 
              transform: scaleX(1);
    }
    50% {
      background-color: #4D8BFF; 
      -webkit-transform: scaleX(0); 
              transform: scaleX(0);
    }
    100% {
      background-color: #4D8BFF; 
      -webkit-transform: scaleX(1); 
              transform: scaleX(1);
    }
  }
  
  @keyframes lineAnimation {
    0% {
      background-color: #00ff00; 
      -webkit-transform: scaleX(0); 
              transform: scaleX(0);
    }
    25% {
      background-color: #00ff00; 
      -webkit-transform: scaleX(1); 
              transform: scaleX(1);
    }
    50% {
      background-color: #4D8BFF; 
      -webkit-transform: scaleX(0); 
              transform: scaleX(0);
    }
    100% {
      background-color: #4D8BFF; 
      -webkit-transform: scaleX(1); 
              transform: scaleX(1);
    }
  }
  
*{
    transition: all 0.3s;
}
.history-main-body{
    width: 100vw;
    height: 800px;
    background-color: #282c34;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.history-box{
    width: 70vw;
    height: 100px;
    background-color: #fff;
    margin-top: 20px;
    border-radius: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.icon-add{
    display: flex;
}
.history-box p{
    margin-left: 15px;
}
.btns{
    margin-right: 30px;
}
.history-box button{
    margin-right: 15px;
    background: none;
    border: none;
    cursor: pointer;
}
.history-main-body .clear{
    width: 150px;
    height: 50px;
    margin-top: 40px;
    border: none;
    border-radius: 40px;
    background-color: rgb(104, 245, 48);
    cursor: pointer;
    color: #fff;
    font-size: 1.2rem;
}
.history-main-body .clear:hover{
    border: 2px solid rgb(104, 245, 48);
    color: rgb(104, 245, 48);
    background-color: #fff;
}
.home-main-body{
    width: 100vw;
    height: 900px;
    background-color: #282c34;
    display: flex;
    flex-direction: column;
    align-items: center;
}

/* **************************************** */

.search-box{
    width: 500px;
    background-color: #dae9fa;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 50px;
    position: absolute;
    z-index: 2;
}
.show-map{
    width: 100vw;
    height: 136vh;
}
.search-box form input{
    width: 300px;
    height: 40px;
    border: 1px solid #B0C7FF;
    border-radius: 40px;
    padding-left: 10px;
    border-top-right-radius: 0px;
    border-bottom-right-radius: 0px;
    outline: none;
    margin-top: 50px;
}
.search-box form button{
    display: inline-block;
    width: 60px;
    height: 42px;
    border: 1px solid #B0C7FF;
    border-radius: 40px;
    border-top-left-radius: 0px;
    border-bottom-left-radius: 0px;
    border-left: none;
    cursor: pointer;
    position: relative;
    top: 2px;
}
.search-box form button i{
    padding-right: 15px;
}
.search-box form input:focus{
    border: 1px solid #4D8BFF;
}

/* **************************************** */

.history-home-box{
    width: 300px;
    height: 350px;
    background-color: #F2F2F2;
    margin-top: 2px;
    margin-right: 50px;
    border-radius: 5px;
}

.history-home-box h3{
    margin: 10px;
}

.history-home{
    width: 300px;
    height: 50px;
    background-color: #FFFFFF;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.history-home-box hr{
    color: #727272;
}
.history-home p i{
    margin-right: 10px;
}
.history-home button{
    border: none;
    background: none;
}
.history-home .btns{
    width: 100px;
    height: 50px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin-right: 10px;
}

/* **********************************MEDIA_QUERIE********************************** */

@media screen and (max-width:502px) {
    .search-box{
        width: 300px;
    }
    .search-box form input{
        width: 200px;
    }

    .history-home-box{
        width: 250px;
        margin-left: 40px;
    }
    .history-home{
        width: 250px;
    }
}

/* ******************************************************* */

.loading-animation-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
  }
  
  .loading-spinner {
    border: 4px solid #f3f3f3; /* Light gray border */
    border-top: 4px solid #3498db; /* Blue border on top */
    border-radius: 50%;
    width: 40px;
    height: 40px;
    -webkit-animation: spin 1s linear infinite;
            animation: spin 1s linear infinite;
    margin: 0 auto;
  }
  
  @-webkit-keyframes spin {
    0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
  @keyframes spin {
    0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
